@import "https://fonts.googleapis.com/css2?family=Outfit&display=swap";
@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@300;700&display=swap";
:root {
  --bg-color: #020b12;
  --second-bg-color: #000;
  --text-color: #fff;
  --second-text-color: #9093b1;
  --social-icon-color: #fff;
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

body {
  color: #fff;
  background-color: #020b12;
  margin: 0;
  padding: 0;
  font-family: Zen Dots, cursive;
}

.video-bg {
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.video-bg video {
  z-index: 1;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.effects {
  object-fit: cover;
  z-index: 2;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, .6);
  position: absolute;
  top: 0;
  left: 0;
}

.video-bg__content h1 {
  color: var(--text-color);
  font-size: 140px;
  transition: all .5s;
}

.video-bg__content h1:hover {
  -webkit-text-stroke: 1px var(--text-color);
  color: rgba(0, 0, 0, 0);
}

.video-bg__content {
  z-index: 2;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.banner-text {
  z-index: 2;
  cursor: pointer;
  color: var(--text-color);
  border: 0;
  font-family: Outfit;
  font-size: 30px;
  position: absolute;
  top: 760px;
}

.banner-text:after {
  content: "";
  height: 2px;
  width: 0%;
  background-color: var(--text-color);
  transition: all .5s ease-in-out;
  display: block;
}

.banner-text:hover:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: var(--text-color);
  display: block;
}

.arrow-down {
  z-index: 2;
  margin: 30px;
  position: absolute;
  top: 820px;
}

.arrow-down span {
  width: 30px;
  height: 30px;
  border-bottom: 5px solid #1e34ff;
  border-right: 5px solid #ff2c2c;
  margin: -16px;
  animation: arrow-down 2s infinite;
  display: block;
  transform: rotate(45deg);
}

.arrow-down span:nth-child(2) {
  animation-delay: -.2s;
}

.arrow-down span:nth-child(3) {
  animation-delay: -.4s;
}

@keyframes arrow-down {
  0% {
    opacity: 0;
    transform: rotate(45deg)translate(-20px, -20px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(45deg)translate(20px, 20px);
  }
}

.page-header {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 9%;
  display: flex;
  top: 0;
  left: 0;
}

.logo {
  color: #f3f0ed;
  font-family: Montserrat;
  font-size: 28px;
  font-weight: 700;
}

.logo span {
  color: #ebebeb;
  transition: all .5s;
  animation: animate 2s infinite;
  display: inline-block;
}

@keyframes animate {
  0% {
    color: #fff;
    margin: 0;
    transform: translateY(0);
  }

  10% {
    color: #2f2bf8;
  }
}

.logo-small {
  color: #f3f0ed;
  font-family: Montserrat;
  font-size: 28px;
  font-weight: 300;
}

.navbar a {
  color: var(--text-color);
  margin-left: 4rem;
  font-size: 1.7rem;
  transition: all .5s;
}

.navbar a:hover, .navbar a.active {
  color: var(--second-text-color);
}

.wrapper {
  display: none;
}

.home {
  min-height: 100vh;
  justify-content: center;
  padding: 180px 9% 2rem;
  display: flex;
}

.name {
  -webkit-text-stroke: 1px var(--text-color);
  color: rgba(0, 0, 0, 0);
  font-family: Montserrat;
  font-size: 5.6rem;
  font-weight: 700;
  line-height: 1.3;
}

.home h3 {
  font-family: Montserrat;
  font-size: 3.2rem;
  font-weight: 700;
}

.home h3:nth-of-type(2) {
  margin-bottom: 2rem;
}

.home_content p {
  font-size: 16px;
}

.developer-text span {
  color: #ebebeb;
  transition: all .5s;
  animation: front-animate 2s infinite;
  display: inline-block;
}

@keyframes front-animate {
  0% {
    color: #fff;
    margin: 0;
    transform: translateY(0);
  }

  10% {
    color: #2f2bf8;
  }
}

.home-img {
  width: 540px;
  height: 400px;
  background: linear-gradient(#04111b, #000);
  margin-left: 52px;
  position: relative;
}

.home-img:before, .home-img:after {
  content: "";
  z-index: -1;
  background: linear-gradient(45deg, #ff2d2d, #2c2cff, #04121d, #ff4d4d, #effcf9, #973d3d, #55fd63, #04121d, #4075e9) 0 0 / 500%;
  animation: amimate 30s infinite;
  position: absolute;
  top: -2px;
  bottom: -2px;
  left: -2px;
  right: -2px;
}

.home-img:after {
  filter: blur(25px);
}

.home-img img {
  width: 540px;
  height: 400px;
  position: absolute;
  bottom: 0;
}

@keyframes amimate {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 300% 0;
  }

  100% {
    background-position: 0 0;
  }
}

.social-media a {
  width: 4rem;
  height: 4rem;
  border: .2rem solid var(--second-text-color);
  color: var(--social-icon-color);
  background: none;
  border-radius: 50%;
  justify-content: center;
  margin: 3rem 1.5rem 3rem 0;
  padding: 12px;
  font-size: 2rem;
  transition: all .5s;
  display: inline-flex;
}

.social-media a:nth-child(1):hover {
  color: #3b5998;
  box-shadow: 0 0 50px #3b5998;
}

.social-media a:nth-child(2):hover {
  background: linear-gradient(#1d2dfd 0%, #833ab4 16%, #b43a88 69%, rgba(252, 222, 69, .97) 100%);
  transition: all .5s;
  box-shadow: -6px -4px 20px #10f, 7px -6px 20px #6802bb, 7px 2px 20px #fa1776, -4px 6px 20px rgba(255, 219, 14, .937);
}

.social-media a:nth-child(3):hover {
  color: #00bfff;
  box-shadow: 0 0 34px #00bfff;
}

.social-media a:nth-child(4):hover {
  color: #fff;
  z-index: -1;
  background: linear-gradient(30deg, #5c5c5c, #030303, #fffefe, #8b8b8b) 0 0 / 300%;
  animation: amimate 20s;
  box-shadow: 0 0 30px #fff;
}

.btn-download {
  background: var(--second-bg-color);
  letter-spacing: .1rem;
  border-radius: 4rem;
  padding: 1rem 2.8rem;
  font-size: 1.6rem;
  font-weight: 600;
  transition: all .3s;
  display: inline-block;
  box-shadow: 0 0 10px #6461ff;
}

.btn-download:hover {
  box-shadow: none;
  background: #1b1b1b;
}

.circle-logo video {
  z-index: 1;
  width: 310px;
  height: 310px;
  object-fit: cover;
  top: 0;
  left: 0;
}

.circle-logo {
  justify-content: center;
  padding: 10px 9% 2rem;
  display: flex;
}

